import { Box, Grid, IconButton, Tooltip, Typography } from "@mui/material";
import React, { useContext } from "react";
import Papa from "papaparse";
import PaginationComponent from "../../Common/PaginationComponent";
import DownloadData from "./DownloadData";
import FilterNavigation from "./FilterNavigation";
import { Context } from "../../../App";
import {
  getTextReuseBooksTSV,
  getTwoBooksData,
} from "../../../services/CorpusMetaData";
import { useNavigate } from "react-router-dom";

const NavigationAndStats = () => {
  const {
    setMetadata,
    setChartData,
    dataLoading,
    setDataLoading,
    setBooks,
    showFilters,
    totalRecords,
    rows,
    status,
    rowsPerPage,
    page,
    checkedBooks,
    setCheckedBooks,
    releaseCode,
    checkedNotification,
    setCheckedNotification,
  } = useContext(Context);
  const navigate = useNavigate();

  const handleRedirectedToChart = async () => {
    setBooks();
    const book1 = checkedBooks[0];

    const book2 = checkedBooks[1];

    const metaDataObject = {
      book1: {
        bookTitle: {
          label: book1?.text?.title_lat_prefered,
          path: book1?.version_uri,
        },
        url: book1?.release_version?.url,
        bookAuthor: book1?.text?.author[0]?.author_lat_prefered,
        wordCount: book1?.release_version?.tok_length,
        versionCode: book1?.version_code,
        annotationStatus: book1?.release_version?.annotation_status,
      },
      book2: {
        bookTitle: {
          label: book2?.text?.title_lat_prefered,
          path: book2?.version_uri,
        },
        bookAuthor: book2?.text?.author[0]?.author_lat_prefered,
        wordCount: book2?.release_version?.tok_length,
        versionCode: book2?.version_code,
        annotationStatus: book2?.release_version?.annotation_status,
      },
      releaseCode: book1?.release_version?.release_code,
      annotationStatus: book2?.release_version?.annotation_status,
    };
    setMetadata(metaDataObject);
    setDataLoading({ ...dataLoading, metadata: false });

    const getTSVLink = async () => {
      const data = await getTextReuseBooksTSV(
        releaseCode,
        book1?.version_uri,
        book2?.version_uri
      );
      console.log(data.results);
      if (data.results.length !== 0) {
        return data.results[0]?.tsv_url;
      } else {
        return false;
      }
    };

    if (await getTSVLink()) {
      const temp_variable = await getTSVLink().replace(
        "passim01122022-v7",
        "passim01122022-v7-light"
      );
      const fetchTSVData = async () => {
        try {
          const response = await getTwoBooksData(temp_variable);
          Papa.parse(response, {
            header: true,
            complete: (result) => {
              setChartData({
                tokens: {
                  first: book1?.release_version?.tok_length,
                  second: book2?.release_version?.tok_length,
                },
                dataSets: result.data,
              });
              setDataLoading({ ...dataLoading, chart: false });
              navigate(`/visualise`);
            },
            error: (error) => {
              console.error("Error parsing CSV:", error);
            },
          });
        } catch (error) {
          console.error("Error occurred:", error);
        }
      };
      fetchTSVData();
    } else {
      setCheckedNotification("Data Not Available");
      setTimeout(() => {
        setCheckedNotification("");
      }, 2500);
    }
  };

  return (
    <div>
      <FilterNavigation showFilters={showFilters} />

      <Grid container>
        <Grid
          item
          container
          sm={1}
          md={4}
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: {
              xs: "space-between",
              sm: "start",
            },
            mb: {
              xs: "10px",
              sm: "0px",
            },
          }}
        >
          {totalRecords ? (
            <Box
              mr={"15px"}
              display={"flex"}
              alignItems={"center"}
              width="max-content"
            >
              <Typography variant="body1" display="block" align="left">
                Total number of results: {totalRecords}
              </Typography>
            </Box>
          ) : (
            ""
          )}

          <Box>
            <DownloadData data={rows} status={status} />
          </Box>

          {checkedBooks.length === 0 ? (
            ""
          ) : checkedBooks.length === 1 ? (
            <Typography ml="10px" color="#fbbf24" sx={{ width: "max-content" }}>
              Select a second book to visualise
            </Typography>
          ) : (
            <Box>
              <Tooltip
                title={
                  checkedBooks.length < 3
                    ? "Pairwise Visualisation"
                    : "One-many Book Visualisation"
                }
                placement="top"
              >
                <span>
                  <IconButton
                    size="large"
                    variant="text"
                    sx={{ fontSize: "15px" }}
                    disabled={checkedBooks.length < 3 ? false : true}
                    onClick={handleRedirectedToChart}
                  >
                    {checkedBooks.length < 3 ? (
                      <i
                        className="fa-solid fa-chart-pie"
                        style={{ color: "green" }}
                      ></i>
                    ) : (
                      <i className="fa-solid fa-chart-column"></i>
                    )}
                  </IconButton>
                </span>
              </Tooltip>
            </Box>
          )}

          {checkedNotification ? (
            <Box>
              <Typography
                variant="body1"
                display="block"
                align="left"
                color={"red"}
                ml={"10px"}
              >
                {checkedNotification}
              </Typography>
            </Box>
          ) : (
            ""
          )}
          {checkedBooks.length !== 0 && (
            <Box ml={checkedBooks.length === 1 ? "10px" : "0px"}>
              <Tooltip title={"Deselect All"} placement="top">
                <span>
                  <IconButton
                    size="large"
                    variant="text"
                    sx={{ fontSize: "15px" }}
                    onClick={() => setCheckedBooks([])}
                  >
                    <i className="fa-regular fa-square-minus"></i>
                  </IconButton>
                </span>
              </Tooltip>
            </Box>
          )}
        </Grid>

        {totalRecords ? (
          <Grid
            item
            sm={1}
            md={8}
            sx={{
              display: {
                xs: "none",
                sm: "block",
              },
            }}
          >
            <PaginationComponent
              totalRecords={totalRecords}
              rowsPerPage={rowsPerPage}
              page={page}
            />
          </Grid>
        ) : (
          ""
        )}
      </Grid>
    </div>
  );
};

export default NavigationAndStats;
