import { TableBody, TableCell, TableRow, Typography } from "@mui/material";
import React from "react";
import VersionIdCell from "./VersionIdCell";
import BookTitleCell from "./BookTitleCell";
import AuthorCell from "./AuthorCell";
import MoreCell from "./MoreCell";
import { useContext } from "react";
import { Context } from "../../../../App";

const TableBodyComponent = ({ classes }) => {
  const { rows } = useContext(Context);
  return (
    <TableBody>
      {rows.map((row, index) => (
        <TableRow
          key={index}
          className={classes.tableCell}
          sx={{
            display: "flex",
            flexDirection: {
              xs: "column",
              sm: "row",
            },
            width: {
              xs: "92%",
              sm: "100%",
            },
            padding: {
              xs: "10px 0px",
              sm: "0px",
            },
            alignItems: {
              xs: "start",
              sm: "center",
            },
            borderWidth: "0px",
            borderColor: "#d1d5db",
            borderStyle: "solid",
            bgcolor:
              row?.release_version?.analysis_priority === "sec"
                ? "#f1f5f9"
                : "white",
            borderBottomWidth: "1px",
            "&:last-child": {
              borderBottomWidth: "0px",
            },
          }}
        >
          <VersionIdCell row={row} classes={classes} />

          <BookTitleCell row={row} classes={classes} />

          <AuthorCell row={row} classes={classes} />

          <TableCell
            className={classes.tableCell}
            sx={{
              width: {
                xs: "100%",
                sm: "10%",
              },
              border: "none",
              display: {
                xs: "flex",
                sm: "block",
              },
              justifyContent: {
                xs: "space-between",
                sm: "center",
              },
            }}
          >
            <Typography>{row?.release_version?.tok_length}</Typography>

            <Typography
              sx={{
                display: {
                  xs: "block",
                  sm: "none",
                },
              }}
            >
              Book Length
            </Typography>
          </TableCell>
          <TableCell
            className={classes.tableCell}
            sx={{
              width: "10%",
              border: "none",
              display: {
                xs: "none",
                sm: "block",
              },
            }}
          >
            {row?.text.author[0]?.date && row?.text?.author[0]?.date}
          </TableCell>

          <MoreCell classes={classes} row={row} />
        </TableRow>
      ))}
    </TableBody>
  );
};

export default TableBodyComponent;
