import { Box, Typography } from "@mui/material";
import React, { useState } from "react";
import { useContext } from "react";
import { Context } from "../../../App";
import WikiDiffButton from "./WikiDiffButton";
import FlipButton from "./FlipButton";
import ToggleButton from "./ToggleButton";

const OtherComponents = ({ item, component }) => {
  const { chartData, booksAlignment } = useContext(Context);
  const [toggle, setToggle] = useState(true);

  return (
    <Box mb="20px">
      <Box
        display={"flex"}
        justifyContent={"space-between"}
        gap={"0px"}
        sx={{
          alignItems: "center",
          height: "60px",
          px: {
            xs: "10px",
            sm: "25px",
          },
          gap: "10px",
          bgcolor: "#F0F0F5",
          borderRadius: "5px",
          position: "relative",
        }}
      >
        <Box
          sx={{
            flexGrow: 1,
            display: "flex",
            alignItems: "center",
            gap: "10px",
          }}
        >
          {item.icon !== "" && (
            <img src={item.icon} alt={item.title} style={{ width: "30px" }} />
          )}
          <Typography textTransform={"none"} color={"black"}>
            {item.title}
          </Typography>
        </Box>
        {item.title === "Books Alignment" && booksAlignment?.s1 && (
          <WikiDiffButton />
        )}
        <Box display="flex" alignItems="center" justifyContent="flex-end">
          {item.title === "Visualisation" && chartData?.dataSets.length ? (
            <FlipButton />
          ) : (
            ""
          )}

          <ToggleButton setToggle={setToggle} toggle={toggle} />
        </Box>
      </Box>
      {toggle ? component : null}
    </Box>
  );
};

export default OtherComponents;
