import { Typography } from "@mui/material";
import { Button } from "@mui/material";
import React, { useContext } from "react";
import ColorKeys from "../BooksAlignment/ColorKeys";
import Hightlights from "../BooksAlignment/Hightlights";
import { Context } from "../../../App";
import { Box } from "@mui/material";

const WikiDiffButton = () => {
  const { setWikiEdDiff, bookIntoRows, setBookIntoRows } = useContext(Context);

  const handleIsRow = () => {
    setBookIntoRows(!bookIntoRows);
  };
  return (
    <>
      <Button onClick={handleIsRow}>
        <Box display="flex" alignItems="center">
          <Typography sx={{ mr: "8px", mt: "2px" }}>
            {bookIntoRows ? (
              <i className="fa-solid fa-square-check"></i>
            ) : (
              <i className="fa-regular fa-square"></i>
            )}
          </Typography>
          <Typography
            ariant="body2"
            sx={{ textTransform: "none", color: "#333" }}
          >
            Divide Into Rows
          </Typography>
        </Box>
      </Button>
      <Button
        sx={{ mr: "25px" }}
        display="flex"
        alignItems="center"
        onClick={() => setWikiEdDiff(true)}
      >
        <Typography sx={{ mr: "8px", mt: "2px" }}>
          <i className="fa-solid fa-eye"></i>
        </Typography>
        <Typography
          variant="body2"
          sx={{ textTransform: "none", color: "#333" }}
        >
          WikiEdDiff
        </Typography>
      </Button>
      <Hightlights />
      <ColorKeys />
    </>
  );
};

export default WikiDiffButton;
