import { Box, TablePagination, Tooltip, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import {
  getPairwiseData,
  getSidePanelData,
  getTwoBooksData,
} from "../../../../../services/CorpusMetaData";
import Papa from "papaparse";
import { useNavigate } from "react-router-dom";
import { Context } from "../../../../../App";
import TableHeader from "./TableHeader";
import { getMetadataObject } from "../../../../../functions/getMetadataObject";

const TextReuseData = ({ fullData, query }) => {
  const navigate = useNavigate();
  const [sortingOrder, setSortingOrder] = useState("-instances_count");
  const [data, setData] = useState([]);
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const {
    setMetadata,
    setChartData,
    dataLoading,
    setDataLoading,
    setBooks,
    setIsOpenDrawer,
    releaseCode,
    setIsFileUploaded,
    setBooksAlignment,
  } = useContext(Context);

  const handlePageChange = (e, newPage) => {
    setPage(newPage);
  };

  const handleRowsPerPageChange = (e) => {
    setRowsPerPage(e.target.value);
  };

  const handleRedirectedToChart = async (value) => {
    setIsFileUploaded(false);
    setDataLoading({
      ...dataLoading,
      uploading: true,
      metadata: true,
      chart: true,
    });
    setMetadata({});
    setChartData({
      tokens: {
        first: "",
        second: "",
      },
      dataSets: [],
    });
    setBooksAlignment({
      s1: "",
      s2: "",
    });
    setBooks();
    const book1 = await getSidePanelData(
      value?.release_code,
      value?.book1?.version_uri
    );

    const book2 = await getSidePanelData(
      value?.release_code,
      value?.book2?.version_uri
    );

    // const metaDataObject = {
    //   book1: {
    //     bookTitle: {
    //       label: book1?.text?.title_lat_prefered,
    //       path: book1?.version_uri,
    //     },
    //     url: book1?.release_version?.url,
    //     bookAuthor: book1?.text?.author[0]?.author_lat_prefered,
    //     wordCount: book1?.release_version?.tok_length,
    //     versionCode: book1?.version_code,
    //     annotationStatus: book1?.release_version?.annotation_status,
    //   },
    //   book2: {
    //     bookTitle: {
    //       label: book2?.text?.title_lat_prefered,
    //       path: book2?.version_uri,
    //     },
    //     bookAuthor: book2?.text?.author[0]?.author_lat_prefered,
    //     wordCount: book2?.release_version?.tok_length,
    //     versionCode: book2?.version_code,
    //     annotationStatus: book2?.release_version?.annotation_status,
    //   },
    //   releaseCode: book1?.release_version?.release_code,
    //   annotationStatus: book2?.release_version?.annotation_status,
    // };
    setMetadata(getMetadataObject(book1, book2, releaseCode));
    setDataLoading({ ...dataLoading, metadata: false });

    const replaceHTTP = value?.tsv_url.replace("http://", "https://");
    const temp_variable = replaceHTTP.replace(
      "passim01122022-v7",
      "passim01122022-v7-light"
    );
    const fetchTSVData = async () => {
      try {
        const response = await getTwoBooksData(temp_variable);
        Papa.parse(response, {
          header: true,
          complete: (result) => {
            setChartData({
              tokens: {
                first: value?.book1?.tok_length,
                second: value?.book2?.tok_length,
              },
              dataSets: result.data,
            });
            setDataLoading({ ...dataLoading, chart: false });
            setIsOpenDrawer(false);
            navigate(`/visualise`);
          },
          error: (error) => {
            console.error("Error parsing CSV:", error);
          },
        });
      } catch (error) {
        console.error("Error occurred:", error);
      }
    };
    fetchTSVData();
    setIsFileUploaded(true);
    setDataLoading({ ...dataLoading, chart: false });
    setDataLoading({ ...dataLoading, uploading: false });
  };

  function useDebounce(value, delay) {
    const [debouncedValue, setDebouncedValue] = useState(value);

    useEffect(() => {
      const timeoutId = setTimeout(() => {
        setDebouncedValue(value);
      }, delay);

      return () => {
        clearTimeout(timeoutId);
      };
    }, [value, delay]);

    return debouncedValue;
  }

  // Inside your component
  const debouncedQuery = useDebounce(query, 300);

  useEffect(() => {
    const releaseCode = localStorage.getItem("release_code");
    let isMounted = true; // Add a flag to track if the component is still mounted

    const fetchData = async () => {
      try {
        const res = await getPairwiseData(
          JSON.parse(releaseCode),
          fullData?.version_uri,
          sortingOrder,
          debouncedQuery
        );

        if (isMounted) {
          setData(res.results);
        }
      } catch (error) {
        // Handle any errors here
      }
    };

    fetchData();

    // Cleanup function to cancel any ongoing tasks when the component unmounts
    return () => {
      isMounted = false; // Set the flag to false when the component unmounts
    };
  }, [sortingOrder, fullData, debouncedQuery]);

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ float: "left", width: "100%" }}>
        <TableHeader
          sortingOrder={sortingOrder}
          setSortingOrder={setSortingOrder}
        />
        {data.length === 0 ? (
          <Box
            display={"flex"}
            justifyContent={"center"}
            py={"20px"}
            pt={"40px"}
            width={"100%"}
          >
            <Typography variant="h4">No Data Available</Typography>
          </Box>
        ) : (
          data.map((item, i) => (
            <Box
              key={i}
              sx={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                margin: "5px 0px",
                padding: "8px 0px",
                borderBottom: "1px solid rgba(224, 224, 224, 1)",
                float: "left",
              }}
            >
              <Typography
                width={"30%"}
                padding={"0px 15px"}
                sx={{
                  wordWrap: "break-word",
                  float: "left",
                  fontSize: "14px",
                  wordBreak: "break-all",
                }}
              >
                {item.book2?.author_lat_prefered}
              </Typography>
              <Typography
                width={"50%"}
                padding={"0px 15px"}
                sx={{
                  wordWrap: "break-word",
                  float: "left",
                  fontSize: "14px",
                  wordBreak: "break-all",
                }}
              >
                {item?.book2?.title_lat_prefered}
              </Typography>
              <Typography
                width={"20%"}
                padding={"0px 15px"}
                textAlign={"center"}
                sx={{
                  wordWrap: "break-word",
                  fontSize: "14px",
                  wordBreak: "break-all",
                }}
              >
                {item?.instances_count}
              </Typography>
              <Box
                width={"10%"}
                padding={"0px 15px"}
                display={"flex"}
                alignItems={"center"}
              >
                <Tooltip placement="top" title={"Visualization"}>
                  <Typography>
                    <button
                      onClick={() => handleRedirectedToChart(item)}
                      style={{
                        background: "none",
                        border: "0px",
                        cursor: "pointer",
                        fontSize: "18px",
                        color: "#7593af",
                        marginRight: "8px",
                      }}
                    >
                      <i className="fa-solid fa-chart-pie"></i>
                    </button>
                  </Typography>
                </Tooltip>

                <Tooltip placement="top" title={"Download CSV"}>
                  <Typography>
                    <a
                      href={item.tsv_url.replace("http://", "https://")}
                      style={{
                        background: "none",
                        border: "0px",
                        cursor: "pointer",
                        fontSize: "18px",
                        color: "#7593af",
                      }}
                    >
                      <i className="fa-solid fa-file-csv"></i>
                    </a>
                  </Typography>
                </Tooltip>
              </Box>
            </Box>
          ))
        )}
      </Box>
      {data.length !== 0 && (
        <TablePagination
          rowsPerPageOptions={[10, 25, 100]}
          component="div"
          onPageChange={(e) => handlePageChange(e)}
          onRowsPerPageChange={(e) => handleRowsPerPageChange(e)}
          count={data.length}
          rowsPerPage={rowsPerPage}
          page={page}
          sx={{ float: "right" }}
        />
      )}
    </Box>
  );
};

export default TextReuseData;
