import * as React from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import { useSearchParams } from "react-router-dom";
import { useEffect } from "react";
import { Box } from "@mui/material";
import { Checkbox } from "@mui/material";
import { Context } from "../../../App";

export default function SetSearchField() {
  const { searchField, setSearchField, normalizedSearch, setNormalizedSearch } =
    React.useContext(Context);
  const [searchParams, setSearchParams] = useSearchParams();

  const handleSearchField = (e) => {
    if (e.target.value) {
      setSearchField(e.target.value);
      const params = Object.fromEntries([...searchParams]);
      setSearchParams({ ...params, search_by: e.target.value });
    } else {
      setSearchField("");
      searchParams.delete("search_by");
      setSearchParams(searchParams);
    }
  };

  const handleNormalize = (e) => {
    e.preventDefault();
    if (normalizedSearch) {
      setNormalizedSearch(false);
      const params = Object.fromEntries([...searchParams]);
      setSearchParams({ ...params, normalize: false });
    } else {
      setNormalizedSearch(true);
      const params = Object.fromEntries([...searchParams]);
      setSearchParams({ ...params, normalize: true });
    }
  };

  useEffect(() => {
    if (searchParams.has("search_by")) {
      setSearchField(searchParams.get("search_by"));
    } else {
      setSearchField("");
    }
  }, [searchParams, setSearchField]);

  useEffect(() => {
    if (searchParams.has("normalize")) {
      setNormalizedSearch(
        searchParams.get("normalize") === "true" ? true : false
      );
    }
  }, [searchParams, setNormalizedSearch]);

  return (
    <Box display="flex" justifyContent="flex-end" width="100%" mt="5px">
      <FormControl>
        <FormControlLabel
          onChange={handleNormalize}
          control={
            <Checkbox
              checked={normalizedSearch}
              sx={{
                "& .MuiSvgIcon-root": {
                  fontSize: 18,
                },
              }}
            />
          }
          label="Normalize Search Terms"
          sx={{
            "& .MuiTypography-root": {
              fontSize: 14,
              userSelect: "none",
            },
          }}
        />
      </FormControl>
      <FormControl sx={{ width: "max-content" }}>
        <RadioGroup
          row
          aria-labelledby="demo-row-radio-buttons-group-label"
          name="row-radio-buttons-group"
          onChange={handleSearchField}
          value={searchField}
        >
          <FormControlLabel
            value="author"
            sx={{
              "& .MuiTypography-root": {
                fontSize: 14,
                userSelect: "none",
              },
            }}
            control={
              <Radio
                sx={{
                  "& .MuiSvgIcon-root": {
                    fontSize: 18,
                  },
                }}
              />
            }
            label="Search By Author"
          />
          <FormControlLabel
            value="title"
            control={
              <Radio
                sx={{
                  "& .MuiSvgIcon-root": {
                    fontSize: 18,
                  },
                }}
              />
            }
            label="Search By Book Title"
            sx={{
              "& .MuiTypography-root": {
                fontSize: 14,
                userSelect: "none",
              },
            }}
          />
        </RadioGroup>
      </FormControl>
    </Box>
  );
}
