import {
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
} from "@mui/material";
import React from "react";
import { useState } from "react";

const CustomFormControlLabel = ({ value, label }) => {
  return (
    <FormControlLabel
      value={value}
      control={
        <Radio
          sx={{
            "& .MuiSvgIcon-root": {
              fontSize: 14,
            },
          }}
        />
      }
      label={label}
      sx={{
        "& .MuiTypography-root": {
          fontSize: 14,
          userSelect: "none",
        },
      }}
    />
  );
};

const Hightlights = () => {
  const [hightlight, setHightlight] = useState("diff");

  const handleColour = (e) => {
    if (e.target.value === "common") {
      setHightlight("common");
      document.documentElement.style.setProperty("--added-color", "white");
      document.documentElement.style.setProperty("--removed-color", "white");
      document.documentElement.style.setProperty("--common-color", "lightblue");
    } else {
      setHightlight("diff");
      document.documentElement.style.setProperty("--added-color", "lightgreen");
      document.documentElement.style.setProperty(
        "--removed-color",
        "lightblue"
      );
      document.documentElement.style.setProperty(
        "--common-color",
        "transparent"
      );
    }
  };

  return (
    <FormControl>
      <FormLabel
        id="row-radio-buttons-group-label"
        sx={{
          fontSize: "14px",
          textAlign: "center",
          mb: "-5px",
          mt: "7px",
        }}
      >
        Text Hightlights
      </FormLabel>
      <RadioGroup
        row
        aria-labelledby="row-radio-buttons-group-label"
        name="row-radio-buttons-group"
        value={hightlight}
        onChange={handleColour}
      >
        <CustomFormControlLabel
          value={"diff"}
          label={"Difference"}
          defaultValue
        />
        <CustomFormControlLabel value={"common"} label={"Common"} />
      </RadioGroup>
    </FormControl>
  );
};

export default Hightlights;
