import Layout from "../components/Common/Layouts";
import { Box } from "@mui/material";
import Header from "../components/Visualisation/Header";
import UploadInput from "../components/Visualisation/UploadInput";
import OtherComponents from "../components/Visualisation/OtherComponent";
import BookMetadata from "../components/Visualisation/Metadata";
import Visual from "../components/Visualisation/Chart";
import React, { createContext, useContext, useEffect } from "react";
import Papa from "papaparse";
import { getBooks } from "../functions/getBooks";
import { Context } from "../App";
import BooksAlignment from "../components/Visualisation/BooksAlignment";
import Books from "../components/Visualisation/Books";
import LoaderIcon from "../components/Common/LoaderIcon";
import CircularInterminate from "../components/Visualisation/CircularIndeterminate";
import { getMetadataObject } from "../functions/getMetadataObject";

const data = [
  {
    title: "Upload TSV File",
    icon: "./icons/upload.svg",
    isDropdown: false,
  },
  {
    title: "Books Metadata",
    icon: "./icons/file.svg",
    isDropdown: true,
  },
  {
    title: "Visualisation",
    icon: "./icons/chart.svg",
    isDropdown: true,
  },
  {
    title: "Books Alignment",
    icon: "./icons/align.svg",
    isDropdown: true,
  },
  {
    title: "Books",
    icon: "./icons/book.svg",
    isDropdown: true,
  },
];

export const VisualisationContext = createContext();

const Visualisation = () => {
  const {
    books,
    setBooks,
    dataLoading,
    setDataLoading,
    flipTimeLoading,
    chartSpecificBar,
    setChartSpecificBar,
    fileState,
    setBooksAlignment,
    setMetadata,
    setChartData,
    releaseCode,
    setReleaseCode,
    chartData,
    metaData,
    isFileUploaded,
    setIsFileUploaded,
    booksAlignment,
  } = useContext(Context);

  const handleUpload = async (value) => {
    setIsFileUploaded(false);
    setDataLoading({
      ...dataLoading,
      uploading: true,
      metadata: true,
      chart: true,
    });
    setMetadata({});
    setChartData({
      tokens: {
        first: "",
        second: "",
      },
      dataSets: [],
    });
    setBooksAlignment({
      s1: "",
      s2: "",
      bw1: 0,
      ew1: 0,
      bw2: 0,
      ew2: 0
    });
    setBooks();
    const book_names = value?.name.split("_");
    const book1 = (await getBooks(releaseCode, book_names)).book1;
    const book2 = (await getBooks(releaseCode, book_names)).book2;

    setMetadata(getMetadataObject(book1, book2, releaseCode));
    setDataLoading({ ...dataLoading, metadata: false });

    Papa.parse(value, {
      header: true,
      skipEmptyLines: true,
      complete: function (result) {
        setChartData({
          tokens: {
            first: book1?.release_version?.tok_length,
            second: book2?.release_version?.tok_length,
          },
          dataSets: result.data,
        });
      },
    });
    setIsFileUploaded(true);
    setDataLoading({ ...dataLoading, chart: false });
    setDataLoading({ ...dataLoading, uploading: false });
  };

  const getToggledComponent = (title) => {
    if (title === "Books Metadata" && metaData?.book1?.bookTitle) {
      if (!dataLoading?.metadata) {
        return <BookMetadata data={metaData} />;
      } else {
        return <CircularInterminate />;
      }
    } else if (title === "Visualisation" && chartData?.dataSets.length) {
      if (!dataLoading?.chart) {
        return (
          <Visual
            chartData={chartData}
            setChartSpecificBar={setChartSpecificBar}
          />
        );
      } else {
        return <CircularInterminate />;
      }
    } else if (title === "Books Alignment" && booksAlignment?.s1) {
      if (!dataLoading?.aligments) {
        return <BooksAlignment />;
      } else {
        return <CircularInterminate />;
      }
    } else if (title === "Books" && books) {
      if (!dataLoading?.books) {
        return <Books chartSpecificBar={chartSpecificBar} />;
      } else {
        return <CircularInterminate />;
      }
    } else {
      return null;
    }
  };

  useEffect(() => {
    const storedReleaseCode = localStorage.getItem("release_code");
    if (storedReleaseCode) {
      setReleaseCode(JSON.parse(storedReleaseCode));
    } else {
      setReleaseCode("2022.2.7");
    }
  }, [setReleaseCode]);

  return (
    <Layout>
      <Box
        mx={"auto"}
        display={"flex"}
        flexDirection={"column"}
        sx={{
          width: {
            xs: "100%",
            xl: "1280px",
          },
          py: "50px",
          float: {
            xs: "left",
            sm: "inherit",
          },
          boxSizing: "border-box",
          position: "relative",
        }}
      >
        <Header />
        {data.map((item, i) => (
          <Box key={i}>
            {item.title === "Upload TSV File" && (
              <UploadInput
                item={item}
                fileState={fileState}
                handleUpload={handleUpload}
              />
            )}
          </Box>
        ))}
        <Box position="relative">
          {flipTimeLoading && (
            <>
              <Box
                sx={{
                  position: "absolute",
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  bgcolor: "#F0F0F5",
                  opacity: 0.6,
                  zIndex: 9999,
                }}
              ></Box>
              <LoaderIcon />
            </>
          )}
          {data.map((item, i) => (
            <Box key={i}>
              {item.title !== "Upload TSV File" &&
                isFileUploaded &&
                (item.title === "Books Alignment" &&
                !booksAlignment?.s1 ? null : (
                  <OtherComponents
                    item={item}
                    component={getToggledComponent(item.title)}
                  />
                ))}
            </Box>
          ))}
        </Box>
      </Box>
    </Layout>
  );
};

export default Visualisation;
