import { Box, IconButton, Modal, Typography } from "@mui/material";
import React, { useState } from "react";
import Contents from "./Contents";
import { Button } from "@mui/material";
import { CircularProgress } from "@mui/material";
import axios from "axios";
import { useContext } from "react";
import { Context } from "../../../App";
import { getParsedBook } from "../../../utility/Helper";

const ExpandView = ({ open, handleClose, data }) => {
  const { metaData, books, setBooks } = useContext(Context);
  const getCompleteBook = data?.bookNumber === 1 ? books?.book1 : books?.book2;

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "100%",
    height: "100vh",
    bgcolor: "background.paper",
    boxSizing: "border-box",
  };

  const [isLoading, setIsLoading] = useState(false);
  const [fontSize, setFontSize] = useState(20);
  const bookNumber = data?.bookNumber;
  const getLastMS = Math.ceil(data?.ms / 300);
  const getTotalContents = getCompleteBook?.content
    ? getCompleteBook?.content
    : {};
  const currentLastMS =
    Object.keys(getTotalContents)[Object.keys(getTotalContents).length - 1];

  const handleNext = async () => {
    setIsLoading(true);
    if (getLastMS > currentLastMS) {
      const msSection = () => {
        return parseInt(currentLastMS) + 300;
      };
      const annotated =
        (bookNumber === 1
          ? metaData?.book1?.annotationStatus
          : metaData?.book2.annotationStatus) === "(not yet annotated)"
          ? ""
          : `.${
              bookNumber === 1
                ? metaData?.book1?.annotationStatus
                : metaData?.book2.annotationStatus
            }`;
      const version = `${
        bookNumber === 1
          ? metaData?.book1?.versionCode
          : metaData?.book2.versionCode
      }-ara1${annotated}`;

      const response = await axios.get(
        `https://raw.githubusercontent.com/OpenITI/i.mech/master/data/${version}-${msSection()
          .toString()
          .padStart(5, "0")}`
      );
      const parsedBook = getParsedBook(response.data);
      const updatedData =
        bookNumber === 1
          ? { ...books?.book1?.content, ...parsedBook }
          : { ...books?.book2?.content, ...parsedBook };

      if (bookNumber === 1) {
        setBooks({
          ...books,
          book1: { ...books?.book1, content: updatedData },
        });
      } else {
        setBooks({
          ...books,
          book2: { ...books?.book2, content: updatedData },
        });
      }
      setIsLoading(false);
    }
  };

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style}>
        <Box
          display={"flex"}
          height={"50px"}
          width={"100%"}
          bgcolor={"#f3f4f6"}
          alignItems={"center"}
          justifyContent={"space-between"}
        >
          <Box px={"20px"}>
            <Typography variant="body1">
              {data?.bookNumber === 1 ? "Book 1" : "Book 2"} {": "}
              {getCompleteBook?.title} {`(${Math.ceil(data?.ms / 300)})`}
            </Typography>
          </Box>

          <Box px={"20px"} display={"flex"} alignItems={"center"} gap={"10px"}>
            <IconButton
              sx={{ fontSize: "18px" }}
              onClick={() => setFontSize(fontSize <= 16 ? 16 : fontSize - 4)}
            >
              <i className="fa-solid fa-minus"></i>
            </IconButton>
            <IconButton
              sx={{ fontSize: "18px" }}
              onClick={() => setFontSize(fontSize >= 40 ? 40 : fontSize + 4)}
            >
              <i className="fa-solid fa-plus"></i>
            </IconButton>
            <IconButton sx={{ fontSize: "18px" }} onClick={handleClose}>
              <i className="fa-solid fa-down-left-and-up-right-to-center"></i>
            </IconButton>
          </Box>
        </Box>
        <Box
          sx={{
            height: "calc(100vh - 50px)",
            width: "100%",
            overflow: "hidden",
            overflowY: "scroll",
            px: {
              sm: "30px",
              lg: "200px",
              xl: "400px",
            },
            boxSizing: "border-box",
          }}
        >
          <Contents
            fullWidth
            data={getCompleteBook}
            fontSize={fontSize}
            bookNumber={data?.bookNumber}
          />
          <Box pb="50px">
            {isLoading && (
              <Box
                sx={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "max-content",
                  py: "10px",
                  pb: "20px",
                }}
              >
                <CircularProgress size={30} />
              </Box>
            )}
            {!isLoading && getLastMS > currentLastMS ? (
              <Button sx={{ width: "100%", mt: "5px" }} onClick={handleNext}>
                Load Next
              </Button>
            ) : (
              ""
            )}

            {getLastMS <= currentLastMS ? (
              <Box
                sx={{
                  display: "flex",
                  width: "100%",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "max-content",
                  py: "60px",
                }}
              >
                <Typography fontWeight={600} variant="h5">
                  The End Of BOOk
                </Typography>
              </Box>
            ) : (
              ""
            )}
          </Box>
        </Box>
      </Box>
    </Modal>
  );
};

export default ExpandView;
