// get book1 and book 2 from text resuse data
export async function getTextReuseBooksTSV(release_code, book1, book2) {
  let RESOURCE = `/${release_code}/text-reuse-stats/`;
  let QUERY_PARAMS = `?book_1=${book1}&book_2=${book2}`;

  const res = await fetch(
    `${
      DEV_ENV
        ? DEV_BASE_URL + RESOURCE + QUERY_PARAMS
        : PROD_BASE_URL + RESOURCE + QUERY_PARAMS
    }`,
    { mode: "cors" }
  );
  const json = await res.json();
  return json;
}

// get pairwise data by version id
export async function getPairwiseData(
  release_code,
  version_uri,
  sortingOrder,
  query
) {
  let RESOURCE = `/${release_code}/text-reuse-stats/`;
  let QUERY_PARAMS = `?book_1=${version_uri}${query && `&search=${query}`}${
    sortingOrder && `&ordering=${sortingOrder}`
  }`;

  const res = await fetch(
    `${
      DEV_ENV
        ? DEV_BASE_URL + RESOURCE + QUERY_PARAMS
        : PROD_BASE_URL + RESOURCE + QUERY_PARAMS
    }`,
    { mode: "cors" }
  );
  const json = await res.json();
  return json;
}

export async function getTwoBooksData(url) {
  const res = await fetch(url, { mode: "cors" });
  const data = await res.text();
  return data;
}

export async function getAggregatedStats() {
  const res = await fetch(
    `${DEV_ENV ? DEV_BASE_URL : PROD_BASE_URL}/aggregatedstats/`,
    { mode: "cors" }
  );
  const data = await res.json();
  return data;
}

// get corpus insight data

export async function getCorpusInsightData(releaseCode) {
  const BASE_URL = "https://dev-kitab-metadata-api.azurewebsites.net/";
  const RELEASE_CODE = releaseCode;

  const res = await fetch(`${BASE_URL}${RELEASE_CODE}/corpusinsights/`, {
    mode: "cors",
  });
  const data = await res.json();
  return data;
}
