import {
  Box,
  CircularProgress,
  Input,
  InputLabel,
  Typography,
} from "@mui/material";
import { useContext } from "react";
import { Context } from "../../App";

const UploadInput = ({ item, handleUpload }) => {
  const { dataLoading } = useContext(Context);

  return (
    <Box width={"100%"} height="300px" display="flex" alignItems="center">
      <InputLabel
        htmlFor={item.title}
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          cursor: "pointer",
          gap: "10px",
          height: "200px",
          px: {
            xs: "10px",
            sm: "25px",
          },
          bgcolor: "white",
          borderRadius: "5px",
          border: "1px dashed grey",
          width: "100%",
        }}
      >
        <Box
          display={"flex"}
          flexDirection="column"
          alignItems={"center"}
          justifyContent="center"
          gap={"15px"}
        >
          {dataLoading?.uploading ? (
            <CircularProgress />
          ) : (
            item.icon !== "" && (
              <i
                className="fa-solid fa-cloud-arrow-up"
                style={{ fontSize: "38px", color: "#9ca3af" }}
              ></i>
            )
          )}
          <Typography textTransform={"none"} color={"grey"} variant="h5">
            {dataLoading?.uploading ? "Getting things ready" : item.title}
          </Typography>
        </Box>
      </InputLabel>
      {item.title === "Upload TSV File" && (
        <Input
          type="file"
          id={item.title}
          sx={{ display: "none" }}
          onChange={(e) => handleUpload(e.target.files[0])}
        />
      )}
    </Box>
  );
};

export default UploadInput;
