import { useCallback, useContext, useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import {
  Box,
  Grid,
  IconButton,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import FilterAltOffIcon from "@mui/icons-material/FilterAltOff";
import FilterClose from "@mui/icons-material/FilterListOff";
import FilterOpen from "@mui/icons-material/FilterList";
import SetSearchField from "./SetSearchField";
import { Context } from "../../../App";

const SearchFilters = ({ handleResetFilters, getQuery }) => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { setFilterPanel, showFilters, searchField } = useContext(Context);
  const [text, setText] = useState("");

  const handleFilterPanel = () => {
    setFilterPanel(!showFilters);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    const params = Object.fromEntries([...searchParams]);
    if (text) {
      setSearchParams({ ...params, search: text });
    } else {
      searchParams.delete("search");
      setSearchParams(searchParams);
    }
  };

  const getSearchQuery = useCallback(() => {
    const delaySearch = setTimeout(() => {
      if (searchParams.get("search")) {
        getQuery(searchParams.get("search"));
      } else {
        getQuery("");
      }
    }, 100);

    return () => clearTimeout(delaySearch);
  }, [getQuery, searchParams]);

  useEffect(() => {
    getSearchQuery();
  }, [getSearchQuery]);

  const updateText = useCallback(() => {
    if (searchParams.has("search")) {
      setText(searchParams.get("search"));
    } else {
      setText("");
    }
  }, [searchParams]);

  const clearSearch = (e) => {
    e.preventDefault();
    setText("");
    getQuery("");
    searchParams.delete("search");
    searchParams.delete("search_by");
    setSearchParams(searchParams);
  };

  useEffect(() => {
    updateText();
  }, [updateText]);

  return (
    <Box display={"flex"} justifyContent={"space-between"} gap={2} my={2}>
      <Grid container item md={12} xs={12}>
        <Grid
          display={"flex"}
          item
          xs={12}
          md={1}
          sx={{
            position: {
              xs: "fixed",
              sm: "relative",
            },
            top: {
              xs: "10px",
              sm: "inherit",
            },
            right: "0px",
          }}
        >
          <Tooltip title="Toggle Filter Sidebar">
            <>
              <IconButton
                onClick={handleFilterPanel}
                sx={{
                  display: "flex",
                }}
              >
                {showFilters ? <FilterClose /> : <FilterOpen />}
              </IconButton>
            </>
          </Tooltip>
          {searchParams.size ? (
            <Tooltip title="Clear Filter">
              <IconButton onClick={handleResetFilters}>
                <FilterAltOffIcon />
              </IconButton>
            </Tooltip>
          ) : (
            ""
          )}
        </Grid>
        <Grid display={"flex"} item xs={12} md={11}>
          <Grid flexGrow={1} className="Search" position={"relative"}>
            <Box>
              <form onSubmit={(e) => handleSearch(e)}>
                <TextField
                  id="outlined-search"
                  label={
                    searchField
                      ? searchField === "author"
                        ? "Search by author"
                        : "Search by title"
                      : "Search by author, title or attributes"
                  }
                  type="search"
                  size="small"
                  value={text}
                  onChange={(e) => setText(e.target.value)}
                  fullWidth={true}
                />
              </form>
              {(searchParams.get("search") || text || searchField) && (
                <Typography
                  sx={{
                    cursor: "pointer",
                    position: "absolute",
                    top: "50%",
                    transform: "translateY(-50%)",
                    right: "15px",
                    mt: "2.5px",
                  }}
                  onClick={(e) => clearSearch(e)}
                >
                  <i className="fa-regular fa-circle-xmark"></i>
                </Typography>
              )}
            </Box>
          </Grid>
        </Grid>
        <SetSearchField />
      </Grid>
    </Box>
  );
};

export default SearchFilters;
