import { Box, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import * as d3 from "d3";
import axios from "axios";
import { Context } from "../../../App";
import { getParsedBook } from "../../../utility/Helper";
import MSToggler from "../OtherComponent/MSToggler";

export const getBook = async (metaData, data, isBook1) => {
  // i.mech contains all books split into 300 milestones;
  // find the section in which the text of the current milestone is stored:
  const msSection = () => {
    const wordCount = isBook1
      ? metaData?.book1?.wordCount
      : metaData?.book2?.wordCount;
    const seq = isBook1 ? Number(data?.seq1) : Number(data?.seq2);
    const lastMs = wordCount / 300 + 1;
    let section = 300;
    for (let i = 0; i < lastMs; i++) {
      if (seq <= section) {
        return section;
      } else {
        section = section + 300;
      }
    }
  };
  const annotated =
    (isBook1
      ? metaData?.book1?.annotationStatus
      : metaData?.book2.annotationStatus) === "(not yet annotated)"
      ? ""
      : `.${
          isBook1
            ? metaData?.book1?.annotationStatus
            : metaData?.book2.annotationStatus
        }`;
  const version = `${
    isBook1 ? metaData?.book1?.versionCode : metaData?.book2.versionCode
  }-ara1${annotated}`;

  // get the text of the relevant part of the book from i.mech:
  const imechUrl =
    "https://raw.githubusercontent.com/OpenITI/i.mech/master/data";
  const response = await axios.get(
    `${imechUrl}/${version}-${msSection().toString().padStart(5, "0")}`
  );
  if (response.status === 200) {
    // parse the content of the i.mech file
    // (object with milestone numbers and milestone text)
    let msTexts = getParsedBook(response.data);

    // get more milestones from i.mech if the desired milestone
    // is the last or first milestone of the i.mech section:
    const seq = isBook1 ? Number(data?.seq1) : Number(data?.seq2);
    let isFirstMs = false;
    let isLastMs = false;
    let msIds = Object.keys(msTexts);
    const firstMsInSection = msIds[0];
    const lastMsInSection = msIds[msIds.length - 1];
    if (Number(seq) + 1 > lastMsInSection) {
      isLastMs = true;
    } else if (Number(seq) - 1 < firstMsInSection) {
      if (Number(seq) > 1) {
        isFirstMs = true;
      }
    }
    if (isFirstMs || isLastMs) {
      // keep the relevant half of the milestones from this section:
      let keepMsIds = [];
      let keepMs = {};
      if (msIds.length > 150) {
        keepMsIds = isLastMs ? msIds.slice(-150) : msIds.slice(0, 150);

        keepMsIds.forEach((key) => {
          keepMs[key] = msTexts[key];
        });
      } else {
        keepMs = msTexts;
      }

      // get more milestones from i.mech:
      console.log("Getting more milestones...");
      const sectionNo = isLastMs ? msSection() + 300 : msSection() - 300;
      console.log(
        `${imechUrl}/${version}-${sectionNo.toString().padStart(5, "0")}`
      );
      const secondResponse = await axios.get(
        `${imechUrl}/${version}-${sectionNo.toString().padStart(5, "0")}`
      );
      if (secondResponse.status === 200) {
        let moreMs = getParsedBook(secondResponse.data);
        // keep only the relevant half of the newly acquired milestones:
        if (moreMs.length > 150) {
          moreMs = isLastMs ? moreMs.slice(0, 150) : moreMs.slice(-150);
        }
        msTexts = { ...keepMs, ...moreMs };
      }
    }
    return msTexts;
  } else {
    return null;
  }
};

const Visual = ({ chartData }) => {
  const {
    metaData,
    setBooks,
    bookSectionRef,
    focusMilestone1,
    focusMilestone2,
    setBooksAlignment,
    isFlipped,
    dataLoading,
    setDataLoading,
  } = useContext(Context);
  const [toolTip, setToolTip] = useState({
    isActive: false,
    layerX: 0,
    layerY: 0,
    data: {
      book1: {
        ms: "",
        pos1: "",
        pos2: "",
      },
      book2: {
        ms: "",
        pos1: "",
        pos2: "",
      },
    },
  });

  var clipPathId = "clipDrawing";
  var clipPath = "url('#clipDrawing')";

  var chartBox,
    svgD3,
    drawingG,
    marksG,
    clipRect,
    x0ScaleNode,
    x1ScaleNode,
    outerHeight = 530,
    outerWidth,
    innerHeight,
    innerWidth,
    width,
    height,
    max,
    refLinesData = null,
    hoverLines = [{}, {}],
    barWidth = 0.5,
    barMaxHeight = 150,
    chunk_size = 300,
    connColor = "#FFCC66",
    connHColor = "#ff9600",
    hoverStrokeWidth = 3,
    selectedLine = null;

  var margin = {
      top: 40,
      right: 20,
      bottom: 20,
      left: 60,
    },
    padding = {
      top: 40,
      right: 0,
      bottom: 40,
      left: 40,
    };

  var book1Bars, connections, book2Bars, brushG;
  var xScale, xScaleIdentity, x0Axis, x1Axis;
  var y0Scale, y0Axis, y1Scale, y1Axis;
  var brushHandle = d3.brushX().on("end", brushEnded);

  var xIdentityDomain,
    currentXDomain,
    duration1 = 700;

  const firstToken = Math.ceil(chartData?.tokens?.first / 300);
  const secondToken = Math.ceil(chartData?.tokens?.second / 300);

  var maxValues = {
    book1: isFlipped ? secondToken : firstToken, //13000,
    book2: isFlipped ? firstToken : secondToken, //13500,
    peek: Math.max(firstToken, secondToken), //13871,
  };

  /////////////// CHART HELPER FUNCTIONS: DRAWING ////////////////////

  function createChart() {
    chartBox = document.getElementById("chartBox");
    svgD3 = d3
      .select(document.getElementById("svgChart"))
      .attr("class", "chartGroup");
    svgD3.selectAll("*").remove();
    brushG = svgD3.append("g").attr("class", "brush");
    drawingG = svgD3
      .append("g")
      .attr("class", "drawing")
      .attr("clip-path", clipPath);
    marksG = svgD3.append("g").attr("class", "markings");

    book1Bars = drawingG.append("g").attr("id", "firstchart");
    connections = drawingG.append("g").attr("class", "connections");
    book2Bars = drawingG.append("g").attr("id", "secondchart");

    xScale = d3.scaleLinear();
    xScaleIdentity = d3.scaleLinear();
    y0Scale = d3.scaleLinear().domain([0, 300]).range([barMaxHeight, 0]);
    y1Scale = d3.scaleLinear().domain([0, 300]).range([0, barMaxHeight]);
    y0Axis = d3.axisLeft(y0Scale).ticks(5);
    y1Axis = d3.axisLeft(y1Scale).ticks(5);

    // - Book1 xAxis Scale::
    x0ScaleNode = marksG
      .append("g")
      .attr("class", "x0 axis")
      .attr("transform", "translate(0," + barMaxHeight + ")");

    // - Book2 xAxis Scale::
    x1ScaleNode = marksG
      .append("g")
      .attr("class", "x1 axis")
      .attr("transform", "translate(0," + barMaxHeight * 2 + ")");

    // - Book1 yAxis Scale::
    marksG.append("g").attr("class", "y0 axis").call(y0Axis);

    // - Book2 xAxis Scale::
    marksG
      .append("g")
      .attr("class", "y1 axis")
      .call(y1Axis)
      .attr("transform", "translate(0," + barMaxHeight * 2 + ")");

    // - Clip Path (Masking) ::
    clipRect = svgD3
      .append("defs")
      .append("clipPath")
      .attr("id", clipPathId)
      .append("rect");

    setLayout();
  }

  function setLayout() {
    outerWidth = chartBox.offsetWidth;
    innerWidth = outerWidth - margin.left - margin.right;
    innerHeight = outerHeight - margin.top - margin.bottom;
    width = innerWidth - padding.left - padding.right;
    height = innerHeight - 20;

    svgD3.attr("width", outerWidth - 30).attr("height", outerHeight);

    drawingG.attr(
      "transform",
      "translate(" + margin.left + "," + margin.top + ")"
    );
    brushG.attr(
      "transform",
      "translate(" + margin.left + "," + margin.top + ")"
    );
    marksG.attr(
      "transform",
      "translate(" + margin.left + "," + margin.top + ")"
    );
    book2Bars.attr("transform", "translate(0,300)");

    clipRect.attr("width", width).attr("height", height);
    // --- Set Scales on Basis of the chartData ::

    max = maxValues;
    xIdentityDomain = [0, max.peek];
    currentXDomain || (currentXDomain = xIdentityDomain);
    xScale.domain(currentXDomain).range([1, width - 1]);
    xScaleIdentity.domain(xIdentityDomain).range([1, width - 1]);
    x0Axis = d3.axisBottom(xScale);
    x1Axis = d3.axisTop(xScale).tickValues([1, max.book2]);
    brushHandle.extent([
      [0, 0],
      [width, height],
    ]);
    refLinesData = [
      { x: 1, y: 0, yScale: y0Scale },
      { x: max.book1, y: 0, yScale: y0Scale },
      { x: 1, y: barMaxHeight * 2, yScale: y1Scale },
      { x: max.book2, y: barMaxHeight * 2, yScale: y1Scale },
    ];
    hoverLines = [
      { x: barMaxHeight, y: 0, yScale: y0Scale, visible: false },
      { x: barMaxHeight, y: barMaxHeight * 2, yScale: y0Scale, visible: false },
    ];
  }

  function drawChart() {
    // - Hover Lines ::
    drawingG
      .selectAll(".dotted-bar-lines")
      .data(hoverLines)
      .enter()
      .insert("line", ":first-child")
      .attr("clip-path", clipPath)
      .attr("class", "dotted-bar-lines")
      .attr("opacity", 0);

    // --- Draw Book1 Bar Chart [START] :::
    var book1BarNodes = book1Bars.selectAll(".bar").data(chartData?.dataSets);

    book1BarNodes
      .enter()
      .append("line")
      .attr("class", "bar")
      .attr("stroke-width", barWidth);

    book1BarNodes.exit().remove();
    // --- Draw Book1 Bar Chart [END] :::

    // --- Draw Connections Curves [START] :::
    var connectionNodes = connections
      .selectAll("path")
      .data(chartData?.dataSets);

    connectionNodes
      .enter()
      .append("path")
      .attr("class", "connection")
      .attr("stroke", connColor);

    connectionNodes.exit().remove();
    // --- Draw Connections Curves [END] :::

    // --- Draw Book2 Bar Chart [START] :::
    var book2BarNodes = book2Bars
      .selectAll(".bar")
      .data(chartData?.dataSets)
      .enter()
      .append("line")
      .attr("class", "bar")
      .attr("stroke-width", barWidth);

    book2BarNodes
      .enter()
      .append("line")
      .attr("class", "bar")
      .attr("stroke-width", barWidth);

    book2BarNodes.exit().remove();
    // --- Draw Book2 Bar Chart [END] :::

    // - Append Brush
    brushG.call(brushHandle).select(".overlay").on("dblclick", restoreCanvas);

    // - Max Marking ::
    marksG
      .selectAll(".max-reference-lines")
      .data(refLinesData)
      .enter()
      .append("line")
      .attr("clip-path", clipPath)
      .attr("class", "max-reference-lines");
  }

  function updateChart(duration) {
    var t = svgD3.transition().duration(duration || 0);

    // - render Bars of Book1 and Book2 ::
    book1Bars
      .selectAll(".bar")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("x1", function (d) {
        return xScale(Number(d.seq1));
      })
      .attr("x2", function (d) {
        return xScale(Number(d.seq1));
      })
      .attr("y1", function (d) {
        return y0Scale(Number(d.bw1));
      })
      .attr("y2", function (d) {
        return y0Scale(Number(d.ew1));
      });

    // - render Connection Curves ::
    connections
      .selectAll("path")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("d", function (d) {
        return (
          "M " +
          xScale(Number(d.seq1)) +
          " 150 C " +
          xScale(Number(d.seq1)) +
          " 250," +
          xScale(Number(d.seq2)) +
          " 220 , " +
          xScale(Number(d.seq2)) +
          " " +
          300
        );
      });

    // - render Bars of Book2 ::
    book2Bars
      .selectAll(".bar")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("x1", function (d) {
        return xScale(Number(d.seq2));
      })
      .attr("x2", function (d) {
        return xScale(Number(d.seq2));
      })
      .attr("y1", function (d) {
        return y1Scale(Number(d.bw2));
      })
      .attr("y2", function (d) {
        return y1Scale(Number(d.ew2));
      });

    // - render X Axis of Book1 ::
    x0Axis.tickValues(
      selectedLine ? [1, Number(selectedLine.seq1), max.book1] : [1, max.book1]
    );
    x0ScaleNode
      .transition(t)
      .call(x0Axis)
      .selectAll("text")
      .attr("x", 10)
      .attr("y", -5)
      .attr("transform", "rotate(90)")
      .style("text-anchor", "start");

    // - render X Axis of Book2 ::
    x1Axis.tickValues(
      selectedLine ? [1, Number(selectedLine.seq2), max.book2] : [1, max.book2]
    );
    x1ScaleNode
      .transition(t)
      .call(x1Axis)
      .selectAll("text")
      .attr("x", -10)
      .attr("y", 2)
      .attr("transform", "rotate(90)")
      .style("text-anchor", "end");

    // - render Reference Lines Min and Max ::
    marksG
      .selectAll(".max-reference-lines")
      .transition(t)
      .attr("x1", function (d) {
        return xScale(d.x);
      })
      .attr("x2", function (d) {
        return xScale(d.x);
      })
      .attr("y1", function (d) {
        return d.yScale(0) + d.y;
      })
      .attr("y2", function (d) {
        return d.yScale(chunk_size) + d.y;
      });

    return t;
  }

  function flipChart(duration) {
    var t = svgD3.transition().duration(duration || 0);

    // - render Bars of Book1 and Book2 ::
    book1Bars
      .selectAll(".bar")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("x1", function (d) {
        return xScale(Number(d.seq2));
      })
      .attr("x2", function (d) {
        return xScale(Number(d.seq2));
      })
      .attr("y1", function (d) {
        return y0Scale(Number(d.bw2));
      })
      .attr("y2", function (d) {
        return y0Scale(Number(d.ew2));
      });

    // - render Connection Curves ::
    connections
      .selectAll("path")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("d", function (d) {
        return (
          "M " +
          xScale(Number(d.seq2)) +
          " 150 C " +
          xScale(Number(d.seq2)) +
          " 250," +
          xScale(Number(d.seq1)) +
          " 220 , " +
          xScale(Number(d.seq1)) +
          " " +
          300
        );
      });

    // - render Bars of Book2 ::
    book2Bars
      .selectAll(".bar")
      .on("mouseover", mouseOver)
      .on("mouseout", mouseOut)
      .on("click", selectLineOnClicked)
      .transition(t)
      .attr("x1", function (d) {
        return xScale(Number(d.seq1));
      })
      .attr("x2", function (d) {
        return xScale(Number(d.seq1));
      })
      .attr("y1", function (d) {
        return y1Scale(Number(d.bw1));
      })
      .attr("y2", function (d) {
        return y1Scale(Number(d.ew1));
      });

    // - render X Axis of Book1 ::
    x0Axis.tickValues(
      selectedLine ? [1, Number(selectedLine.seq1), max.book1] : [1, max.book1]
    );
    x0ScaleNode
      .transition(t)
      .call(x0Axis)
      .selectAll("text")
      .attr("x", 10)
      .attr("y", -5)
      .attr("transform", "rotate(90)")
      .style("text-anchor", "start");

    // - render X Axis of Book2 ::
    x1Axis.tickValues(
      selectedLine ? [1, Number(selectedLine.seq2), max.book2] : [1, max.book2]
    );
    x1ScaleNode
      .transition(t)
      .call(x1Axis)
      .selectAll("text")
      .attr("x", -10)
      .attr("y", 2)
      .attr("transform", "rotate(90)")
      .style("text-anchor", "end");

    // - render Reference Lines Min and Max ::
    marksG
      .selectAll(".max-reference-lines")
      .transition(t)
      .attr("x1", function (d) {
        return xScale(d.x);
      })
      .attr("x2", function (d) {
        return xScale(d.x);
      })
      .attr("y1", function (d) {
        return d.yScale(0) + d.y;
      })
      .attr("y2", function (d) {
        return d.yScale(chunk_size) + d.y;
      });

    return t;
  }

  /////////////// CHART HELPER FUNCTIONS: INTERACTIONS ////////////////////

  function brushEnded(e) {
    if (!e.sourceEvent) return; // Only transition after input.
    var sel = e.selection;
    if (!sel) {
      return;
    }

    currentXDomain = sel.map(function (d) {
      return Math.round(xScale.invert(d));
    });
    xScale.domain(currentXDomain);
    zoom();
  }

  function restoreCanvas() {
    if (selectedLine) {
      selectedLine = null;
      makeLinesVisible();
    }
    currentXDomain = xIdentityDomain;
    xScale.domain(currentXDomain);
    setTimeout(zoom, 0);
  }

  function zoom() {
    brushG.call(brushHandle.move, null);
    isFlipped ? flipChart(duration1) : updateChart(duration1);
  }

  function focusOnLine(d1) {
    var a = Number(d1.seq1);
    var b = Number(d1.seq2);
    var min = Math.min(a, b) - xScaleIdentity.invert(5);
    var max = Math.max(a, b) + xScaleIdentity.invert(5);

    currentXDomain = [min, max];

    xScale.domain(currentXDomain);
    zoom();
  }

  function getConnections() {
    return connections.selectAll("path");
  }
  function getBars() {
    return drawingG.selectAll("#firstchart .bar, #secondchart .bar");
  }
  function filterSelected(d1, nodesD3) {
    return nodesD3.filter(function (d) {
      return d === d1;
    });
  }

  async function mouseOver(e, d1) {
    // set data to tooltip
    if (e) {
      setToolTip({
        isActive: true,
        layerX: e.layerX,
        layerY: e.layerY,
        data: {
          book1: {
            ms: d1?.seq1,
            pos1: d1?.bw1,
            pos2: d1?.ew1,
          },
          book2: {
            ms: d1?.seq2,
            pos1: d1?.bw2,
            pos2: d1?.ew2,
          },
        },
      });
    } else {
      createChart();
      setLayout();
      drawChart();
    }

    filterSelected(d1, getConnections())
      .attr("stroke", connHColor)
      .attr("stroke-width", hoverStrokeWidth)
      .attr("opacity", null);
    filterSelected(d1, getBars())
      .attr("stroke-width", hoverStrokeWidth)
      .attr("opacity", null);
    // - render Dotted Bars for book1 and book2 on hover/click ::
    hoverLines[0].x = Number(isFlipped ? d1.seq2 : d1.seq1);
    hoverLines[1].x = Number(isFlipped ? d1.seq1 : d1.seq2);
    drawingG
      .selectAll(".dotted-bar-lines")
      .attr("x1", function (d) {
        return xScale(d.x);
      })
      .attr("x2", function (d) {
        return xScale(d.x);
      })
      .attr("y1", function (d) {
        return d.yScale(0) + d.y;
      })
      .attr("y2", function (d) {
        return d.yScale(chunk_size) + d.y;
      })
      .attr("opacity", null);
  }

  function mouseOut(e, d1) {
    // clear data from tooltip
    setToolTip({
      isActive: false,
      layerX: 0,
      layerY: 0,
      data: {
        book1: {
          ms: "",
          pos1: "",
          pos2: "",
        },
        book2: {
          ms: "",
          pos1: "",
          pos2: "",
        },
      },
    });
    if (selectedLine === d1) return;

    filterSelected(d1, getConnections())
      .transition()
      .attr("stroke", connColor)
      .attr("stroke-width", null)
      .attr("opacity", opacityOnMouseOut);

    filterSelected(d1, getBars())
      .transition()
      .attr("stroke-width", barWidth)
      .attr("opacity", opacityOnMouseOut);

    drawingG.selectAll(".dotted-bar-lines").attr("opacity", 0);

    function opacityOnMouseOut(d) {
      return d.hidden ? 0.1 : null;
    }
  }

  async function selectLineOnClicked(e, d1) {
    setDataLoading({ ...dataLoading, books: true });
    setBooksAlignment({
      s1: d1?.s1,
      s2: d1?.s2,
      bw1: d1?.bw1,
      ew1: d1?.ew1,
      bw2: d1?.bw2,
      ew2: d1?.ew2,
    });
    if (d1 === selectedLine) return;

    selectedLine && clearSelectedLine();
    selectedLine = d1;

    getConnections()
      .each(function hideOthers(d) {
        d.hidden = d !== d1;
      })
      .filter(filterHidden)
      .attr("opacity", 0.1);

    getBars().filter(filterHidden).attr("opacity", 0.1);

    drawingG.selectAll(".dotted-bar-lines").attr("opacity", 0);

    setTimeout(focusOnLine, 0, d1);

    function filterHidden(d) {
      return d.hidden;
    }

    setBooks({
      book1: {
        title: metaData?.book1?.bookTitle?.label,
        content: await getBook(metaData, d1, true),
        ms: d1?.seq1,
      },
      book2: {
        title: metaData?.book2?.bookTitle?.label,
        content: await getBook(metaData, d1, false),
        ms: d1?.seq2,
      },
    });
    setDataLoading({ ...dataLoading, books: false });

    if (bookSectionRef.current) {
      bookSectionRef.current.scrollIntoView(true);
      setTimeout(() => {
        if (focusMilestone1.current) {
          focusMilestone1.current.scrollIntoView(true);
        }
      }, 1000);
      setTimeout(() => {
        if (focusMilestone2.current) {
          focusMilestone2.current.scrollIntoView(true);
        }
      }, 1000);
    }
  }

  function clearSelectedLine() {
    var d2 = selectedLine;
    selectedLine = null;
    d2.hidden = true;
    mouseOut(d2);
  }

  function makeLinesVisible() {
    getConnections()
      .each(function hideOthers(d) {
        delete d.hidden;
      })
      .attr("stroke", connColor)
      .attr("stroke-width", null)
      .attr("opacity", null);

    getBars().attr("stroke-width", barWidth).attr("opacity", null);

    drawingG.selectAll(".dotted-bar-lines").attr("opacity", 0);
  }

  /////////////////// CHART MAIN FUNCTIONS ///////////////////

  const normalChart = () => {
    createChart();
    setLayout();
    drawChart();
    getConnections();
    getBars();
    updateChart();
  };

  const flippedChart = () => {
    createChart();
    setLayout();
    drawChart();
    getConnections();
    getBars();
    flipChart();
  };

  /////////////////////////////////////////////////////////////////////

  // Draw the chart:

  useEffect(() => {
    isFlipped ? flippedChart() : normalChart();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isFlipped]);

  return (
    <Box
      sx={{
        px: {
          xs: "0px",
          sm: "30px",
        },
      }}
    >
      <Box display="flex" justifyContent="flex-end" mt="20px">
        <MSToggler
          isTop
          selectLineOnClicked={selectLineOnClicked}
          mouseOver={mouseOver}
        />
      </Box>
      <Box id={"chartBox"} sx={{ width: "100%", position: "relative" }}>
        <svg id={"svgChart"} style={{ position: "relative" }}></svg>
        {toolTip.isActive && (
          <Box
            sx={{
              minWidth: "180px",
              maxWidth: "200px",
              borderRadius: "5px",
              bgcolor: "#2862a5",
              color: "white",
              position: "absolute",
              top: `${toolTip.layerY - 20}px`,
              left: `${toolTip?.layerX + 15}px`,
              padding: "10px",
              boxSizing: "border-box",
              opacity: 0.8,
            }}
          >
            <Typography sx={{ fontSize: "12px" }} fontWeight={"bold"}>
              Book 1{" "}
              {isFlipped
                ? metaData?.book2?.bookTitle?.label
                : metaData?.book1?.bookTitle?.label}
            </Typography>
            <Typography sx={{ fontSize: "12px" }}>
              MS#{" "}
              {isFlipped ? toolTip?.data?.book2?.ms : toolTip?.data?.book1?.ms}
            </Typography>
            <Typography sx={{ fontSize: "12px" }}>
              Token Positions{" "}
              {`(${
                isFlipped
                  ? toolTip?.data?.book2?.pos1
                  : toolTip?.data?.book1?.pos1
              }-${
                isFlipped
                  ? toolTip?.data?.book2?.pos2
                  : toolTip?.data?.book1?.pos2
              })`}
            </Typography>
            <Typography sx={{ fontSize: "12px" }} fontWeight={"bold"}>
              Book 2{" "}
              {isFlipped
                ? metaData?.book1?.bookTitle?.label
                : metaData?.book2?.bookTitle?.label}
            </Typography>
            <Typography sx={{ fontSize: "12px" }}>
              MS#{" "}
              {isFlipped ? toolTip?.data?.book1?.ms : toolTip?.data?.book2?.ms}
            </Typography>
            <Typography sx={{ fontSize: "12px" }}>
              Token Positions{" "}
              {`(${
                isFlipped
                  ? toolTip?.data?.book1?.pos1
                  : toolTip?.data?.book2?.pos1
              }-${
                isFlipped
                  ? toolTip?.data?.book1?.pos2
                  : toolTip?.data?.book2?.pos2
              })`}
            </Typography>
          </Box>
        )}
      </Box>
      <Box display="flex" justifyContent="flex-end">
        <MSToggler
          selectLineOnClicked={selectLineOnClicked}
          mouseOver={mouseOver}
        />
      </Box>
    </Box>
  );
};

export default Visual;
