import "./App.css";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { green, red } from "@mui/material/colors";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import HomePage from "./pages/HomePage";
import Insight from "./pages/Insight";
import Visualisation from "./pages/Visualisation";
import CorpusMetadata from "./pages/CorpusMetadata";
import { createContext, useRef, useState } from "react";
import DiffViewer from "./pages/DiffViewer";
import LeftSidePanel from "./components/CorpusMetadata/Drawer";

const theme = createTheme({
  palette: {
    primary: {
      main: "#2863A5",
    },
    secondary: {
      main: green[500],
    },
    neutral: {
      main: "#7593af",
      contrastText: "#fff",
    },
    iconColor: {
      main: "#fff",
    },
    mARkdown: {
      main: green[500],
    },
    notYetAnnotated: {
      main: red[500],
    },
    inProgress: {
      main: "#2863A5",
    },
    completed: {
      main: "#ed6c02",
    },
    deactivateColor: {
      main: "#d3d3d3",
    },
    pri: {
      main: "#fdeded",
    },
    invisible: {
      main: "#fff",
    },
    mainNav: {
      main: "#eee",
    },
  },
  typography: {
    fontFamily: [
      "Amiri",
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
    ].join(","),
  },
});

export const Context = createContext();

function App() {
  // ===== State - Metatable States =====
  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [rows, setRows] = useState([]);
  const [releaseCode, setReleaseCode] = useState("2022.2.7");
  const [status, setStatus] = useState(false);
  const [query, setQuery] = useState("");
  const [searchField, setSearchField] = useState("");
  const [normalizedSearch, setNormalizedSearch] = useState(true);
  const [sortingOrder, setOrderingOrder] = useState("");
  const [analysisPriority, setAnalysisPriority] = useState(false);
  const [annotationFilter, setAnnotationFilter] = useState({
    completed: false,
    inProgress: false,
    mARkdown: false,
    notYetAnnotated: false,
  });

  const [totalRecords, setTotal] = useState(0);

  const [isOpenDrawer, setIsOpenDrawer] = useState(false);
  const [versionDetail, setVersionDetails] = useState({});
  const [tabIndex, setTabIndex] = useState(0);

  const [checkedBooks, setCheckedBooks] = useState([]);
  const [checkedNotification, setCheckedNotification] = useState("");
  const [showFilters, setFilterPanel] = useState(false);

  const [metaData, setMetadata] = useState({});
  const [chartData, setChartData] = useState({
    tokens: {
      first: "",
      second: "",
    },
    dataSets: [],
  });
  const [books, setBooks] = useState();
  const [dataLoading, setDataLoading] = useState({
    uploading: false,
    metadata: false,
    chart: false,
    books: false,
    aligments: false,
  });

  // ===== Visualization Page's State =====

  const bookSectionRef = useRef();
  const [isFileUploaded, setIsFileUploaded] = useState(false);
  const [isFlipped, setIsFlipped] = useState(false);
  const [flipTimeLoading, setFlipTimeLoading] = useState(false);
  const focusMilestone1 = useRef();
  const focusMilestone2 = useRef();
  const [chartSpecificBar, setChartSpecificBar] = useState({});
  const [booksAlignment, setBooksAlignment] = useState({
    s1: "",
    s2: "",
    bw1: 0,
    ew1: 0,
    bw2: 0,
    ew2: 0,
  });
  const [focusedDataIndex, setFocusedDataIndex] = useState(null);
  const [bookIntoRows, setBookIntoRows] = useState(false);
  const [selectedLine, setSelectedLine] = useState({});
  const [showWikiEdDiff, setWikiEdDiff] = useState(false);

  const toggleSidePanel = (val, tIndex) => {
    setTabIndex(tIndex);
    setVersionDetails(val);
    setIsOpenDrawer(true);
  };

  return (
    <Context.Provider
      value={{
        page,
        setPage,
        rowsPerPage,
        setRowsPerPage,
        rows,
        setRows,
        releaseCode,
        setReleaseCode,
        status,
        setStatus,
        query,
        setQuery,
        searchField,
        setSearchField,
        normalizedSearch,
        setNormalizedSearch,
        sortingOrder,
        setOrderingOrder,
        analysisPriority,
        setAnalysisPriority,
        annotationFilter,
        setAnnotationFilter,
        totalRecords,
        setTotal,
        versionDetail,
        setVersionDetails,
        tabIndex,
        setTabIndex,
        checkedBooks,
        setCheckedBooks,
        checkedNotification,
        setCheckedNotification,
        showFilters,
        setFilterPanel,
        metaData,
        setMetadata,
        chartData,
        setChartData,
        books,
        setBooks,
        dataLoading,
        setDataLoading,
        bookSectionRef,
        isFlipped,
        setIsFlipped,
        flipTimeLoading,
        setFlipTimeLoading,
        focusMilestone1,
        focusMilestone2,
        chartSpecificBar,
        setChartSpecificBar,
        booksAlignment,
        setBooksAlignment,
        selectedLine,
        setSelectedLine,
        showWikiEdDiff,
        setWikiEdDiff,
        isOpenDrawer,
        setIsOpenDrawer,
        toggleSidePanel,
        isFileUploaded,
        setIsFileUploaded,
        bookIntoRows,
        setBookIntoRows,
        focusedDataIndex,
        setFocusedDataIndex,
      }}
    >
      <ThemeProvider theme={theme}>
        <div className="App">
          <Router>
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/metadata/" element={<CorpusMetadata />} />
              <Route path="/metadata/:version" element={<CorpusMetadata />} />
              <Route path="/insight" element={<Insight />} />
              <Route path="/visualise" element={<Visualisation />} />
              <Route path="/diffViewer" element={<DiffViewer />} />
            </Routes>

            <LeftSidePanel />
          </Router>
        </div>
      </ThemeProvider>
    </Context.Provider>
  );
}

export default App;
