import React, { useState } from "react";
import { Box, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import TextReuseData from "./TextReuseData/index.js";
import { TextField } from "@mui/material";

const DownloadSrt = ({ fullData }) => {
  const [query, setQuery] = useState("");
  const data = [
    {
      header: `Text Reuse Folder (${fullData?.version_code})`,
      link: `https://dev.kitab-project.org/passim01122022-v7/${
        fullData?.version_uri &&
        fullData?.version_uri.split(".").slice(-1).toString()
      }`,
    },
    {
      header: `Version URI`,
      body: fullData?.version_uri,
    },
    {
      header: `OpenITI Corpus Release`,
      body: fullData?.release_version?.release_code,
    },
  ];

  return (
    <Box sx={{ width: "100%" }}>
      <Box>
        {data.length !== 0 &&
          data.slice(1).map((item, index) => (
            <Box
              key={index}
              sx={{
                width: "100%",
                display: "flex",
                margin: "5px 0px",
                borderBottom: "1px solid rgba(224, 224, 224, 1)",
              }}
            >
              <Box margin={"5px 0px"} width={"35%"} sx={{ textAlign: "left" }}>
                {item.header}
              </Box>
              <Box margin={"5px 0px"}>
                {item.link ? (
                  <Link
                    to={item.link}
                    target="_blank"
                    rel="noreferrer"
                    style={{ textDecoration: "none" }}
                  >
                    {item.body}
                  </Link>
                ) : (
                  item.body
                )}
              </Box>
            </Box>
          ))}
      </Box>
      <Box py={"30px"}>
        <Box display="flex" alignItems="center" justifyContent="space-between">
          <Typography>Text Reuse Data:</Typography>
          <TextField
            id="outlined-search"
            label={"Search"}
            type="search"
            size="small"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            sx={{
              maxWidth: "50%",
              minWidth: "30%",
            }}
          />
        </Box>
        <TextReuseData fullData={fullData} query={query} setQuery={setQuery} />
      </Box>
      <Box>
        {data.length !== 0 &&
          data.slice(0, 1).map((item, index) => (
            <Box
              key={index}
              sx={{
                width: "100%",
                display: "flex",
                margin: "5px 0px",
                borderBottom: "1px solid rgba(224, 224, 224, 1)",
              }}
            >
              <Box margin={"5px 0px"} width={"35%"} sx={{ textAlign: "left" }}>
                <i
                  className="fa-solid fa-folder"
                  style={{
                    marginLeft: "15px",
                    marginRight: "8px",
                    color: "#7593af",
                  }}
                ></i>
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.header}
                </Link>
              </Box>
              <Box margin={"5px 0px"}>{item.link ? "" : item.body}</Box>
            </Box>
          ))}
      </Box>
    </Box>
  );
};

export default DownloadSrt;
