import { Box, Button, CircularProgress, Typography } from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import MileStone from "./MileStone";

const Contents = ({ data, fullWidth, fontSize, bookNumber }) => {
  const ref = useRef();
  const [content, setContent] = useState({});
  const [botomLoader, setBottomLoader] = useState(false);
  const [topLoader, setTopLoader] = useState(false);
  const [bottomEnd, setBottomEnd] = useState(false);

  const loadNextMilestone = () => {
    const value = Object.keys(content)[Object.keys(content).length - 1];
    const allKeys = Object.keys(data?.content).filter((item) => {
      return Number(item) === Number(value) + 1;
    });

    if (allKeys.length === 1) {
      setBottomLoader(true);
      setTimeout(() => {
        if (
          Number(value) + 1 ===
          Object.keys(content)[Object.keys(content).length - 1]
        ) {
          setBottomEnd(true);
        }
        setContent({
          ...content,
          [Number(value) + 1]: data?.content[Number(value) + 1],
        });
        setBottomLoader(false);
      }, 300);
    }
  };

  const loadPreviousMilestone = () => {
    if (Number(Object.keys(content)[0]) - 1 !== Number(Object.keys(data)[0])) {
      setTopLoader(true);
      setTimeout(() => {
        setContent({
          ...content,
          [Object.keys(content)[0] - 1]:
            data?.content[Object.keys(content)[0] - 1],
        });
        setTopLoader(false);
      }, 300);
    }
  };

  useEffect(() => {
    if (ref?.current) {
      ref.current.scrollTop = 0;
    }
    // empty the content object:
    setContent({});

    let initialData = {};
    /*
    // add the previous milestone to the content object:
    initialData[Number(data?.ms) - 1] =
      data?.content[`${Number(data?.ms) - 1}`];
    */
    // add the current milestone to the content object:
    initialData[Number(data?.ms)] = data?.content[data?.ms];
    /*
    // add the next milestone to the content object:
    initialData[Number(data?.ms) + 1] =
      data?.content[`${Number(data?.ms) + 1}`];
    */
    // store the selected milestones in the state: 
    setContent(initialData);
  }, [data]);

  return (
    <Box
      mb={fullWidth ? null : 3}
      p={fullWidth ? "30px" : "0px 20px"}
      textAlign={"right"}
      height={fullWidth ? "max-content" : 400}
      overflow={"hidden"}
      sx={{
        overflowY: "scroll",
        direction: "rtl",
      }}
      ref={ref}
    >
      {topLoader && (
        <Box
          sx={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
            alignItems: "center",
            height: "max-content",
            py: "10px",
            pb: "20px",
          }}
        >
          <CircularProgress size={30} />
        </Box>
      )}
      {!fullWidth && (
        <div>
          {!topLoader && (
            <Button
              sx={{ width: "100%", mb: "5px" }}
              onClick={loadPreviousMilestone}
            >
              Load Previous Milestone
            </Button>
          )}
        </div>
      )}
      {Object.keys(fullWidth ? data?.content : content).map((item, i) => (
        <div key={i}>
          <MileStone
            fullWidth={fullWidth}
            data={data}
            content={content}
            fontSize={fontSize}
            item={item}
            bookNumber={bookNumber}
          />
        </div>
      ))}
      {bottomEnd && (
        <Box
          sx={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
            alignItems: "center",
            height: "max-content",
            py: "60px",
          }}
        >
          <Typography fontWeight={600} variant="h5">
            End Of The Book
          </Typography>
        </Box>
      )}

      {(!fullWidth || bottomEnd) && (
        <div>
          {!botomLoader && (
            <Button
              sx={{ width: "100%", mt: "5px" }}
              onClick={loadNextMilestone}
            >
              Load Next Milestone
            </Button>
          )}
        </div>
      )}
      {botomLoader && (
        <Box
          sx={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
            alignItems: "center",
            height: "max-content",
            py: "10px",
            pb: "20px",
          }}
        >
          <CircularProgress size={30} />
        </Box>
      )}
    </Box>
  );
};

export default Contents;
