import { Typography } from "@mui/material";
import React, { useContext, useRef, useState } from "react";
import { Box } from "@mui/material";
import { Context } from "../../../../App";
import { arTokRegex } from "../../../../assets/js/openITI.js"

const MileStone = ({
  fullWidth,
  fontSize,
  item,          // milestone number
  data,          // Object: {content: <300 milestones>, ms: <ms_no(str)>, title: <book_title(str)>}
  content,       // Object: {ms_no: ms_str}  (one milestone, or more if user clicked load button)
  bookNumber,
}) => {
  const { focusMilestone1, focusMilestone2, booksAlignment, setBooksAlignment } = useContext(Context);
  const typographyRef = useRef(null);
  const [notification, setNotification] = useState(false);
  const msText = fullWidth ? data?.content[item] : content[item];

  const getRef = () => {
    if (bookNumber === 1) {
      return Number(data?.ms) === Number(item) ? focusMilestone1 : null;
    } else {
      return Number(data?.ms) === Number(item) ? focusMilestone2 : null;
    }
  };

  const handleDoubleClick = () => {
    if (typographyRef.current) {
      const plainText = typographyRef.current.textContent;
      navigator.clipboard
        .writeText(plainText)
        .then(() => {
          console.log("Copied to clipboard");
          setNotification(true);
          setTimeout(() => {
            setNotification(false);
          }, 1500);
        })
        .catch((err) => {
          console.error("Error copying text: ", err);
        });
    }
  };


  // extract the alignment from a milestone text
  const extractAlignment = (msText, startTok, endTok) => {
    // get the start index of each Arabic token in the milestone text:
    let tokenMatches = msText.matchAll(arTokRegex);
    let tokenStartIndices = [];
    for (const m of tokenMatches) {
      tokenStartIndices.push(m.index);
    }
    
    // map the start and end tokens to characters in the text:
    let startChar = tokenStartIndices[startTok];
    let endChar = tokenStartIndices[endTok];

    // split the text:
    let beforeAlignment = msText.slice(0,startChar);
    let alignment = msText.slice(startChar,endChar);
    let afterAlignment = msText.slice(endChar, msText.length);

    return [beforeAlignment, alignment, afterAlignment];
    
  };

  // Split the text of a milestone to emphasize the alignment:
  const splitMilestone = (msText) => {

    // get the start and end token of the alignment:
    let startTok = bookNumber === 1 
      ? booksAlignment.bw1 
      : booksAlignment.bw2;  
    let endTok = bookNumber === 1 
      ? booksAlignment.ew1 
      : booksAlignment.ew2;

    let resp = extractAlignment(msText, startTok, endTok);
    let [beforeAlignment, alignment, afterAlignment] = resp;

    // set the book alignment string: 
    if (bookNumber === 1) {
      booksAlignment["s1"] = alignment;
    } else {
      booksAlignment["s2"] = alignment;
    }
    setBooksAlignment(booksAlignment);

    return `${beforeAlignment}<br><br>${alignment}<br>^^^^^^^^^^^^<br>${afterAlignment}`;
  }

  return (
    <Box
      display="flex"
      position="relative"
      width="100%"
      sx={{
        mb: "30px",
        "&:hover": {
          "& .msClass": {
            visibility: "visible",
          },
        },
      }}
    >
      <Box
        fontSize={fullWidth ? `${fontSize - 4}px` : "16px"}
        ref={getRef()}
        onDoubleClick={handleDoubleClick}
        className="msClass"
        sx={{
          width: "20px",
          position: "absolute",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          flexWrap: "wrap",
          alignItems: "center",
          textAlign: "center",
          top: 0,
          left: "100%",
          ml: "0px",
          bottom: 0,
          visibility: "hidden",
          cursor: "pointer",
          userSelect: "none",
          bgcolor: "#f3f4f6",
          color: "black",
        }}
      >
        <Typography sx={{ fontSize: "11px" }}>MS</Typography>
        <Typography sx={{ fontSize: "11px" }}>{item}</Typography>
      </Box>
      {notification && (
        <Typography
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            bgcolor: "#bbf7d0",
            boxShadow: "0px 0px 5px 0px grey",
            p: "5px",
          }}
        >
          Text Copied
        </Typography>
      )}
      <Typography
        ref={typographyRef}
        sx={{
          pr: "10px",
          //bgcolor: Number(data?.ms) === Number(item) ? "lightgreen" : "",
        }}
        fontSize={fullWidth ? `${fontSize}px` : "20px"}
        dangerouslySetInnerHTML={{
          __html: (Number(data?.ms) === Number(item)) ? splitMilestone(msText) : msText,
        }}
      />
    </Box>
  );
};

export default MileStone;
