import { Box } from "@mui/material";
import React from "react";
import { getGHRepo } from "../../../../utility/Helper";
import {
  displayAuthorDetailsRelatedPlaces,
  displayNamesAuthor,
} from "../ColumnDisplayName";
import RelatedPersonsTable from "./RelatedPersonsTable";
import { Link } from "react-router-dom";
import CopyToClipboard from "../CopyToClipboard";
import { Typography } from "@mui/material";

const AuthorDetails = ({ fullData }) => {
  const author = fullData?.text?.author[0];

  const data = [
    {
      header: displayNamesAuthor.author_uri,
      body: author?.author_uri,
      link: getGHRepo(author?.date) + author?.author_uri,
      copy: true,
    },
    {
      header: displayNamesAuthor.author_ar,
      body: author?.author_ar_prefered,
    },
    {
      header: displayNamesAuthor.author_lat,
      body: author?.author_lat_prefered,
    },
    {
      header: displayNamesAuthor.authorDateAH,
      body: author?.date_AH,
    },
    {
      header: displayNamesAuthor.authorDateCE,
      body: author?.date_CE,
    },
    {
      header: displayNamesAuthor.authorDateString,
      body: author?.date_str,
    },
    {
      header: displayNamesAuthor.notes,
      body: "N/A",
    },
    {
      header: displayNamesAuthor.related_persons,
    },
  ];

  return (
    <Box sx={{ width: "100%" }}>
      {data.map((item, index) => (
        <Box
          key={index}
          sx={{
            width: "100%",
            display: "flex",
            margin: "5px 0px",
            borderBottom: "1px solid rgba(224, 224, 224, 1)",
          }}
        >
          {item.header === "Related Persons" ? (
            author && (
              <Box width={"100%"} margin={"5px 0px"}>
                {item.header}
                <RelatedPersonsTable data={author?.name_elements} />
              </Box>
            )
          ) : (
            <Box sx={{ width: "25%", margin: "5px 0px" }}>{item.header}</Box>
          )}

          {item.header === "Related Persons" ? (
            ""
          ) : (
            <Box
              sx={{
                width: "75%",
                display: "flex",
                flexWrap: "wrap",
                alignItems: "center",
              }}
            >
              {item.link ? (
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.body}
                </Link>
              ) : (
                item.body
              )}
              {item.copy && <CopyToClipboard data={item.body} />}
            </Box>
          )}
        </Box>
      ))}
      <Box mt="30px">
        {fullData?.text?.author[0]?.related_places.length !== 0 && (
          <Typography variant="h5">Related Places</Typography>
        )}

        {fullData?.text?.author[0]?.related_places.length !== 0 && (
          <Box
            margin={"0px"}
            sx={{
              display: "flex",
              flexWrap: "wrap",
              alignItems: "center",
              width: "75%",
            }}
          >
            {`${author?.author_lat_prefered} ${author?.related_places[0]?.relation_type_name} ${author?.related_places[0]?.related_place_uri}`}
          </Box>
        )}

        {fullData?.text?.author[0] &&
          fullData?.text?.author[0]?.related_places.map((rpObject, rpIndex) => (
            <Box key={rpIndex} py="10px">
              {Object.keys(rpObject).map((rpObjectItem, rpObjectIndex) => (
                <Box
                  key={rpObjectIndex}
                  sx={{
                    width: "100%",
                    display: "flex",
                    justifyContent: "space-between",
                    margin: "0px",
                    borderBottom: "1px solid rgba(224, 224, 224, 1)",
                  }}
                >
                  <Box margin={"0px"} width={"20%"}>
                    {
                      displayAuthorDetailsRelatedPlaces[
                        Object.keys(rpObject)[rpObjectIndex]
                      ]
                    }
                  </Box>
                  <Box
                    margin={"0px"}
                    sx={{
                      display: "flex",
                      flexWrap: "wrap",
                      alignItems: "center",
                      width: "75%",
                    }}
                  >
                    {Object.values(rpObject)[rpObjectIndex]}
                  </Box>
                </Box>
              ))}
            </Box>
          ))}
      </Box>
    </Box>
  );
};

export default AuthorDetails;
