import { Button } from "@mui/material";
import { Box } from "@mui/material";
import React from "react";
import { useContext } from "react";
import { Context } from "../../../App";
import { FormControl } from "@mui/material";
import { InputLabel } from "@mui/material";
import { Select } from "@mui/material";
import { MenuItem } from "@mui/material";

const MSToggler = ({ isTop, selectLineOnClicked, mouseOver }) => {
  const { chartData, focusedDataIndex, setFocusedDataIndex } =
    useContext(Context);

  const handleFocusData = (actionType) => {
    if (chartData?.dataSets.length !== 0) {
      if (actionType === "plus") {
        if (focusedDataIndex < chartData?.dataSets.length - 1) {
          if (focusedDataIndex === null) {
            const value = 0;
            setFocusedDataIndex(value);
            mouseOver(null, chartData?.dataSets[value]);
            selectLineOnClicked(null, chartData?.dataSets[value]);
          } else {
            const value = focusedDataIndex + 1;
            setFocusedDataIndex(value);
            mouseOver(null, chartData?.dataSets[value]);
            selectLineOnClicked(null, chartData?.dataSets[value]);
          }
        } else {
          const value = chartData?.dataSets.length - 1;
          setFocusedDataIndex(value);
          mouseOver(null, chartData?.dataSets[value]);
          selectLineOnClicked(null, chartData?.dataSets[value]);
        }
      } else {
        if (focusedDataIndex < 1) {
          const value = 0;
          setFocusedDataIndex(value);
          mouseOver(null, chartData?.dataSets[value]);
          selectLineOnClicked(null, chartData?.dataSets[value]);
        } else {
          const value = focusedDataIndex - 1;
          setFocusedDataIndex(value);
          mouseOver(null, chartData?.dataSets[value]);
          selectLineOnClicked(null, chartData?.dataSets[value]);
        }
      }
    }
  };

  const handleChange = (event) => {
    const value = event.target.value;
    setFocusedDataIndex(value);
    mouseOver(null, chartData?.dataSets[value]);
    selectLineOnClicked(null, chartData?.dataSets[value]);
  };

  return (
    <Box display="flex" alignItems="center" bgcolor="#F0F0F5" py="20px">
      <Button
        sx={{ width: "40px", height: "40px", fontSize: "18px" }}
        onClick={() => handleFocusData("minus")}
      >
        <i className="fa-solid fa-angle-left"></i>
      </Button>
      <Box
        width="70px"
        height="40px"
        bgcolor="white"
        display="flex"
        alignItems="center"
        justifyContent="center"
        borderRadius="3px"
        position="relative"
      >
        <FormControl sx={{ minWidth: 80 }} size="small">
          <InputLabel id="demo-simple-select-autowidth-label">
            {isTop ? "MS-1" : "MS-2"}
          </InputLabel>
          <Select
            labelId="demo-simple-select-autowidth-label"
            id="demo-simple-select-autowidth"
            value={focusedDataIndex}
            onChange={handleChange}
            autoWidth
            label={isTop ? "MS-1" : "MS-2"}
          >
            {chartData?.dataSets.map((item, key) => (
              <MenuItem key={key} value={key}>
                {isTop ? item.seq1 : item.seq2}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Box>

      <Button
        sx={{
          width: "40px",
          height: "40px",
          fontSize: "18px",
        }}
        onClick={() => handleFocusData("plus")}
      >
        <i className="fa-solid fa-angle-right"></i>
      </Button>
    </Box>
  );
};

export default MSToggler;
